'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/Card';
import { DataTable } from '@/components/DataTable';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import { mockUsers } from '@/data/mock';
import { Modal } from '@/components/ui/Modal';
import { Input } from '@/components/ui/Input';
import { Checkbox } from '@/components/ui/Checkbox';
import type { User } from '@/types';
import { Search, Plus, Trash2, Edit2, Download, Upload } from 'lucide-react';

const STORAGE_KEY = 'admin-users';

function loadInitialUsers(): User[] {
  try {
    const raw = localStorage.getItem(STORAGE_KEY);
    if (raw) return JSON.parse(raw) as User[];
  } catch (e) {
    // ignore
  }
  return Object.values(mockUsers).map((u) => ({ ...u }));
}

export default function Users() {
  const [users, setUsers] = useState<User[]>([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const [form, setForm] = useState({ name: '', email: '', role: 'student' as User['role'], department: '' });
  const [searchTerm, setSearchTerm] = useState('');
  const [roleFilter, setRoleFilter] = useState('all');
  const [selectedUsers, setSelectedUsers] = useState<string[]>([]);

  useEffect(() => {
    const initial = loadInitialUsers();
    setUsers(initial);
  }, []);

  useEffect(() => {
    try {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(users));
    } catch (e) {
      // ignore
    }
  }, [users]);

  // Filter users based on search and role filter
  const filteredUsers = users.filter(user => {
    const matchSearch = searchTerm === '' || 
      user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      user.email.toLowerCase().includes(searchTerm.toLowerCase());
    
    const matchRole = roleFilter === 'all' || user.role === roleFilter;
    
    return matchSearch && matchRole;
  });

  const openAdd = () => {
    setEditingUser(null);
    setForm({ name: '', email: '', role: 'student', department: '' });
    setIsModalOpen(true);
  };

  const openEdit = (user: User) => {
    setEditingUser(user);
    setForm({ 
      name: user.name, 
      email: user.email, 
      role: user.role,
      department: (user as any).department || ''
    });
    setIsModalOpen(true);
  };

  const saveUser = () => {
    if (!form.name || !form.email) return alert('Name and email are required');

    if (editingUser) {
      setUsers((prev) => prev.map((u) => (u.id === editingUser.id ? { ...u, ...form } : u)));
      alert('User updated successfully!');
    } else {
      const id = `user-${Date.now()}`;
      setUsers((prev) => [{ id, name: form.name, email: form.email, role: form.role, ...form } as any, ...prev]);
      alert('User created successfully!');
    }

    setIsModalOpen(false);
  };

  const deleteUser = (id: string) => {
    if (!confirm('Delete this user? This action cannot be undone.')) return;
    setUsers((prev) => prev.filter((u) => u.id !== id));
    alert('User deleted');
  };

  const bulkDelete = () => {
    if (selectedUsers.length === 0) {
      alert('Please select users to delete');
      return;
    }
    if (!confirm(`Delete ${selectedUsers.length} users? This cannot be undone.`)) return;
    setUsers((prev) => prev.filter((u) => !selectedUsers.includes(u.id)));
    setSelectedUsers([]);
    alert('Users deleted successfully!');
  };

  const bulkChangeRole = (newRole: User['role']) => {
    if (selectedUsers.length === 0) {
      alert('Please select users');
      return;
    }
    setUsers((prev) =>
      prev.map((u) => (selectedUsers.includes(u.id) ? { ...u, role: newRole } : u))
    );
    setSelectedUsers([]);
    alert(`Role updated for ${selectedUsers.length} users!`);
  };

  const exportUsers = () => {
    const data = filteredUsers.map(u => ({
      name: u.name,
      email: u.email,
      role: u.role,
      department: (u as any).department || 'N/A',
    }));

    const headers = ['Name', 'Email', 'Role', 'Department'];
    const csv = [
      headers.join(','),
      ...data.map(d => headers.map(h => `"${String((d as any)[h.toLowerCase()] ?? '')}"`).join(',')),
    ].join('\n');

    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `users-${new Date().toISOString().split('T')[0]}.csv`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  };

  const toggleUserSelection = (userId: string) => {
    setSelectedUsers(prev =>
      prev.includes(userId)
        ? prev.filter(id => id !== userId)
        : [...prev, userId]
    );
  };

  const stats = {
    total: users.length,
    students: users.filter(u => u.role === 'student').length,
    teachers: users.filter(u => u.role === 'teacher').length,
    admins: users.filter(u => u.role === 'dean').length,
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-start">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">User Management</h1>
          <p className="text-gray-600 dark:text-gray-400 mt-2">Manage system users and assign roles</p>
        </div>
        <Button variant="primary" onClick={openAdd}>Add User</Button>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>All Users</CardTitle>
          <CardDescription>System users and their roles</CardDescription>
        </CardHeader>
        <CardContent>
          <DataTable
            columns={[
              { key: 'name' as any, label: 'Name' },
              { key: 'email' as any, label: 'Email' },
              {
                key: 'role' as any,
                label: 'Role',
                render: (value: any, _row: User) => (
                  <Badge variant="secondary">{String(value).charAt(0).toUpperCase() + String(value).slice(1)}</Badge>
                ),
              },
              {
                key: 'id' as any,
                label: 'Actions',
                render: (_: any, row: User) => (
                  <div className="flex gap-2">
                    <Button variant="outline" size="sm" onClick={(e) => { e.stopPropagation(); openEdit(row); }}>Edit</Button>
                    <Button variant="danger" size="sm" onClick={(e) => { e.stopPropagation(); deleteUser(row.id); }}>Delete</Button>
                  </div>
                ),
              },
            ]}
            data={users.map((u) => ({ ...u, id: u.id }))}
          />
        </CardContent>
      </Card>

      <Modal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} title={editingUser ? 'Edit User' : 'Add User'}>
        <div className="space-y-4">
          <Input label="Full name" value={form.name} onChange={(e) => setForm({ ...form, name: e.target.value })} />
          <Input label="Email" value={form.email} onChange={(e) => setForm({ ...form, email: e.target.value })} />
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1.5">Role</label>
            <select className="w-full px-4 py-2 rounded-lg border border-gray-300 dark:border-gray-600 bg-white dark:bg-gray-700 text-gray-900 dark:text-white" value={form.role} onChange={(e) => setForm({ ...form, role: e.target.value as User['role'] })}>
              <option value="student">Student</option>
              <option value="teacher">Teacher</option>
              <option value="dean">Admin</option>
            </select>
          </div>

          <div className="flex justify-end gap-2">
            <Button variant="secondary" onClick={() => setIsModalOpen(false)}>Cancel</Button>
            <Button variant="primary" onClick={saveUser}>{editingUser ? 'Save Changes' : 'Create User'}</Button>
          </div>
        </div>
      </Modal>
    </div>
  );
}
